<template>
  <el-dialog
    class="popup-credits"
    title="积分组成详情"
    :visible.sync="show"
    destroy-on-close
    v-loading="loading"
    @closed="onClose"
  >
    <div v-loading="loading">
      <info-block>
        <info-item label="用户名称：">用户1</info-item>
        <info-item label="积分值："><span style="color: #19be6b">{{ -36066 | fmtMoney }}</span></info-item>
        <info-item label="方式：">自动</info-item>
        <info-item label="类型：">担当1</info-item>
        <info-item label="活动/核销平台"><el-link type="primary" @click="popupActIntro(1)">活动2</el-link></info-item>
        <info-item label="用户行为：">商品兑换</info-item>
        <info-item label="商品名称：">拖鞋</info-item>
        <info-item label="时间：">2019-04-15 12:00</info-item>
      </info-block>

      <el-table
        :data="list"
        stripe
        size="mini"
        style="width: 100%">
        <el-table-column label="序号" align="center">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column label="积分账户名称" prop="account" align="center" min-width="120px"></el-table-column>
        <el-table-column label="活动名称" prop="activity" align="center">
          <template slot-scope="scope">
            <el-link type="primary" @click="popupActIntro(scope.row.actId)">{{ scope.row.activity }}</el-link>
          </template>
        </el-table-column>
        <el-table-column label="用户行为" prop="behevior" align="center"></el-table-column>
        <el-table-column label="获取时间" prop="getTime" align="center" min-width="150px"></el-table-column>
        <el-table-column label="积分数值" prop="credits" align="center">
          <template slot-scope="scope">
            <span style="color: #19be6b">{{ scope.row.credits | creditStatus }}</span>
          </template>
        </el-table-column>
      </el-table>

      <el-divider></el-divider>

      <div class="dialog-footer">
        <el-button size="small" plain @click="show = false">关 闭</el-button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'

export default {
  name: 'popup-activity-intro',
  model: {
    prop: 'visible',
    event: 'change'
  },
  components: {
    InfoBlock,
    InfoItem
  },
  props: {
    visible: Boolean,
    rid: [Number, String]
  },
  data () {
    return {
      show: false,
      loading: false,
      list: [
        {
          account: 'CF部积分账户',
          actId: 1,
          activity: '活动1',
          behevior: '注册认证',
          getTime: '2019-04-15 12:00',
          credits: 100
        }
      ],
      selectedAId: 1
    }
  },
  watch: {
    visible (val) {
      this.show = val
    }
  },
  filters: {
    creditStatus (val) {
      const v = Number(val)
      if (isNaN(v) || v === 0) return val

      return (v > 0 ? '+' : '') + v
    }
  },
  methods: {
    onClose () {
      this.$emit('update:visible', this.show)
    },

    popupActIntro (aid) {
      this.$emit('popupAct', aid)
    }
  }
}
</script>

<style lang="scss" scoped>
.popup-credits {
  .el-link {
    font-size: 12px;
  }

  .el-table::before {
    display: none;
  }

  ::v-deep .el-table__header th { background-color: #fafafa; border-bottom: 0; }
  ::v-deep .el-table__body td { border-bottom: 0; }

  .dialog-footer {
    text-align: center;

    .el-button {
      min-width: 100px;
    }
  }
}
</style>
