<template>
  <div class="credit-detail">
    <el-new-card>
      <template slot="header">{{ $route.meta.name }}</template>

      <!-- 查询条件 -->
      <el-form inline size="small" :model="searchForm" ref="searchForm">
        <el-form-item prop="plat">
          <el-select v-model="searchForm.plat" placeholder="选择活动/核销平台">
            <el-option label="全部" value=""></el-option>
            <el-option label="活动1" :value="2"></el-option>
            <el-option label="活动2" :value="3"></el-option>
            <el-option label="积分商城" :value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="behavior">
          <el-select v-model="searchForm.behavior" placeholder="选择用户行为">
            <el-option label="全部" :value="1"></el-option>
            <el-option label="注册认证" :value="2"></el-option>
            <el-option label="抽奖-一等奖" :value="3"></el-option>
            <el-option label="抽奖-二等奖" :value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="commodity">
          <el-input v-model="searchForm.commodity" placeholder="输入商品名称"></el-input>
        </el-form-item>
        <el-form-item prop="user">
          <el-input v-model="searchForm.user" placeholder="输入用户名称"></el-input>
        </el-form-item>
        <el-form-item prop="mode">
          <el-select v-model="searchForm.mode" placeholder="选择方式">
            <el-option label="全部" value=""></el-option>
            <el-option label="自动" :value="2"></el-option>
            <el-option label="手动" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="type">
          <el-select v-model="searchForm.type" placeholder="选择类型">
            <el-option label="全部" value=""></el-option>
            <el-option label="发行" :value="2"></el-option>
            <el-option label="核销" :value="3"></el-option>
            <el-option label="到期" :value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="account">
          <el-select v-model="searchForm.account" placeholder="选择积分组成中包含的积分账户" style="width: 240px">
            <el-option label="CF部积分账户" :value="1"></el-option>
            <el-option label="MK部积分账户" :value="2"></el-option>
            <el-option label="MA部积分账户" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="dateRange">
          <el-new-date-picker v-model="searchForm.dateRange"></el-new-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button plain type="primary" icon="el-icon-search" @click="onSearch">查询</el-button>
          <el-button plain icon="el-icon-refresh-right" @click="onReset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-new-card>

    <el-new-card>
      <template slot="header">
        积分明细列表
        <el-button plain size="small" icon="el-icon-upload2">导出</el-button>
      </template>

      <el-new-table :apiName="apiName" :data="searchForm" ref="table">
        <el-table-column prop="credits" label="积分数值" align="center" min-width="100">
          <template slot-scope="scope">
            <span :style="scope.row.credits | creditStatusColor">{{ scope.row.credits | creditStatus }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="model" label="方式" align="center"></el-table-column>
        <el-table-column prop="type" label="类型" align="center"></el-table-column>
        <el-table-column prop="user" label="用户名称" align="center" min-width="120"></el-table-column>
        <el-table-column prop="plat" label="活动/核销平台" align="center" min-width="120">
          <template slot-scope="scope">
            <el-link type="primary" v-if="scope.row.plat.includes('活动')" @click="popupActIntro(scope.row.aid)">{{ scope.row.plat }}</el-link>
            <span v-else>{{ scope.row.plat || '-' }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="behavior" label="用户行为" align="center" min-width="120"></el-table-column>
        <el-table-column prop="commodity" label="商品名称" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.commodity || '-' }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="account" label="积分组成" align="center" min-width="120">
          <template slot-scope="scope">
            <el-link type="primary" @click="popupCredits(scope.row.id)">查看详情</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="时间" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.date }}</span>
          </template>
        </el-table-column>
      </el-new-table>
    </el-new-card>

    <popup-act-intro :visible.sync="actIntroVisible" :aid="selectedAId"></popup-act-intro>
    <popup-credits :visible.sync="creditsVisible" :rid="selectedRId" @popupAct="popupActIntro"></popup-credits>
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import ElNewTable from '@/components/ElNewTable'
import ElNewDatePicker from '@/components/ElNewDatePicker'
import PopupActIntro from '@/views/popups/PopupActIntro'
import PopupCredits from '@/views/popups/PopupCredits'

export default {
  name: 'credit-detail',
  components: {
    ElNewCard,
    ElNewTable,
    ElNewDatePicker,
    PopupActIntro,
    PopupCredits
  },
  data () {
    return {
      apiName: 'serviceGetPointsDetail',
      searchForm: { // 搜索条件
        plat: null,
        behavior: null,
        commodity: null,
        user: null,
        mode: null,
        type: null,
        account: null,
        dateRange: []
      },
      actIntroVisible: false, // 活动简介弹窗控制
      selectedAId: 1, // 弹窗的活动的id
      creditsVisible: false, // 积分组成详情弹窗控制
      selectedRId: 1 // 弹窗的积分组成详情的id
    }
  },
  filters: {
    creditStatus (val) {
      const v = Number(val)
      if (isNaN(v) || v === 0) return val

      return (v > 0 ? '+' : '') + v
    },

    creditStatusColor (val) {
      const v = Number(val)
      if (isNaN(v) || v === 0) return ''

      return { color: v > 0 ? '#19be6b' : '#ed4014' }
    }
  },
  methods: {
    onSearch () {
      this.$refs.table.reset()
    },

    onReset () {
      this.$refs.searchForm.resetFields()
    },

    popupActIntro (aid) {
      this.selectedAId = aid
      this.actIntroVisible = true
    },

    popupCredits (rid) {
      this.selectedRId = rid
      this.creditsVisible = true
    }
  }
}
</script>

